.about{
    padding-bottom:48px;
    background-color: white;
    border-radius: 10px;
    margin-bottom: 40px;
    margin-top: 40px;
}
.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height:80%;
}

.about-info{
    font-size: 17px;
    line-height: 28px;
    margin-bottom: 15px;
     margin-left: 20px;
     font-weight: bold;   
}

.picture{
    height:300px;
    width:280px;
    border-radius: 50%;
    margin:20px;
}

.changeText{
    font-size:38px;
    color:red;
    margin-bottom: 15px;
    margin-left: 20px;
    
}
@media only screen and (max-width:720px){
    .about-top{
        flex-direction:column;
        justify-content: center;
        align-items:center;
    }
    .picture{
        height:200px;
        width:180px;
        margin:20px;
    }
}