.web{
    display:flex;
    justify-content: center;
    width:120%;
}

.web-option{
    font-size:20px;
    margin-right:16px;
    font-weight:700;
}

.web-option:hover{
    background-color: red;
    color:white;
    border-radius: 10px;
    padding: 2px;
}

.web-option a{
    text-decoration: none;
    color:black;
    display: flex;
    align-items: center;
}

.web-option a:hover{
    color:white;
}

.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

.web-option:last-child{
    margin-right:0px;
}