.footer{
    padding:12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
}

.line{
    background-image: linear-gradient(to right top, #af2745, #5918d1, #e05858, #eb1010, #d876ac, #0c19d1, #f5bb50, #ff0800, #ff1100, #ed5f00, #00b6e3, #5564ed);
    height:6px;
    width:40%;
    border-radius: 3px;
    margin:16px 0px;
    align-self: flex-start;
}

.color{
    color:rgb(36, 80, 224);
    font-weight: bold;   
}